export default {
	props: {
		val: {
			type: [Number, String],
			default: ''
		},
		maxlength: {
			type: Number,
			default: 140
		},
		type: {
			type: String,
			default: 'text'
		},
		password: {
			type: Boolean,
			default: false
		},
		placeholder: {
			type: String,
			default: ''
		},
		labelText: {
			type: String,
			default: ''
		},
		inputPRight: {
			type: String
		}
	},
	data() {
		return {
			itemInputFocus: false,
		};
	},
	methods: {
		formItemFocus() {
			this.$emit('onFocus');
			this.itemInputFocus = true;
		},
		formItemBlur() {
			this.$emit('onBlur');
			this.itemInputFocus = false;
		},
		formItemHandleInput (event) {
			this.$emit('update:val', event.target.value);
		}
	}
};
